h1, h2, h3, h4, h5 { font-family:"Helvetica", sans-serif; color:#133C63; }
a { color:#000; }
a:hover { color:#133C63; }
h1 { font-weight:600; font-size:2em; }
h2 { margin-bottom:20px; font-size:1.8em; }
h4 { margin:0 0 10px; }
img { height:auto; }

.row { display:flex; flex-wrap:wrap; margin:0 auto; }
.navigation { padding:10px;  flex:0 0 auto; width:15%; flex-basis:15%; }
.nav-link { display:block; padding:0.5rem 1rem; color:#133C63; text-decoration:none; transition:color .15s ease-in-out,background-color.15s ease-in-out,border-color .15s ease-in-out; }
.viewer { padding:10px; flex:0 0 auto; width:75%; flex-basis:75%; }
 .col-lg-4,
.viewer .col-lg-4 { width:33.33334%; flex-basis:33.33334%; }
.viewer .col-lg-4 div { margin:10px; }

table { width:100%; margin-bottom:1rem; color:#212529; vertical-align:top; border:solid 1px #dee2e6; border-collapse:collapse; }
tbody, td, tfoot, th, thead, tr { border:solid 0 inherit; }
table > thead { vertical-align:bottom; }
table > :not(caption) > * { border-right:solid 1px #dee2e6; }
th { text-align:inherit; text-align:-webkit-match-parent; }
table > :not(caption) > * > * { padding: 0.5rem 0.5rem; border-bottom:1px solid #dee2e6; border-right:1px solid #dee2e6; vertical-align: middle; }
table ul { padding-left:20px; }

.pagination { display:flex; padding-left:0; list-style:none; }
.page-link { position:relative; display:block; color:#0d6efd; text-decoration:none; background-color:#fff; border:1px solid #dee2e6; transition:color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out; padding:0.375rem 0.75rem; }
.page-item.active .page-link { z-index:3; color:#FFF; background:#133C63; border-color:#133C63; }
.page-item:first-child .page-link { border-top-left-radius:0.25rem; border-bottom-left-radius:0.25rem; }
.page-item:not(:first-child) .page-link { margin-left:-1px; }
.page-item:last-child .page-link { border-top-right-radius: 0.25rem; border-bottom-right-radius: 0.25rem; }
.page-link:hover { z-index:2; color:#133C63; background: #e9ecef; border-color:#dee2e6; }

button, input, select, textarea { margin:0; font-family:inherit; font-size:inherit; line-height:inherit; }
.form-control { display:block; width:100%; padding:0.375rem 0.75rem; font-size:1rem; font-weight:400; line-height:1.5; color:#212529; background-color:#fff; background-clip:padding-box; border:1px solid #ced4da; -webkit-appearance:none; -moz-appearance:none; appearance:none; border-radius:0.25rem; transition:border-color .15s ease-in-out,box-shadow .15s ease-in-out; box-sizing:border-box; }

.was-validated :valid~.valid-feedback, 
.was-validated :valid~.valid-tooltip { display:block; }

.form-control.is-valid, .was-validated .form-control:valid {
    border-color: #198754;
    padding-right: calc(1.5em + 0.75rem);
    /* background-image: url(data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23198754' d='M2.3 6.73L.6 4.53c-.4-1.04.46-1.4 1.1-.8l1.1 1.4 3.4-3.8c.6-.63 1.6-.27 1.2.7l-4 4.6c-.43.5-.8.4-1.1.1z'/%3e%3c/svg%3e); */
    background-repeat: no-repeat;
    background-position: right calc(0.375em + 0.1875rem) center;
    background-size: calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);
}
.form-control.is-invalid, 
.was-validated .form-control:invalid {
    border-color: #dc3545;
    padding-right: calc(1.5em + 0.75rem);
    /* background-image: url(data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 12 12' width='12' height='12' fill='none' stroke='%23dc3545'%3e%3ccircle cx='6' cy='6' r='4.5'/%3e%3cpath stroke-linejoin='round' d='M5.8 3.6h.4L6 6.5z'/%3e%3ccircle cx='6' cy='8.2' r='.6' fill='%23dc3545' stroke='none'/%3e%3c/svg%3e); */
    background-repeat: no-repeat;
    background-position: right calc(0.375em + 0.1875rem) center;
    background-size: calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);
}

.invalid-feedback, .valid-feedback { display: none; width:100%; margin-top: 0.25rem; font-size:.875em; }
.valid-feedback { color:#198754; }
.invalid-feedback { color: #dc3545; }
.rounded { border-radius: 0.25rem; }
.p-2 { padding:0.75rem; }
.p-3 { padding:1rem; }
.p-4 { padding:1.5rem; }

.pt-1 { padding-top:0.5rem; }
.pt-2 { padding-top:0.75rem; }
.pt-3 { padding-top:1rem; }

.pb-1 { padding-bottom:0.5rem; }
.pb-2 { padding-bottom:0.75rem; }
.pb-3 { padding-bottom:1rem; }
.pb-4 { padding-bottom:1.25rem; }
.pb-5 { padding-bottom:1.5rem; }

.ml-0 { margin-left:0; }
.ml-1 { margin-left:0.5rem; }
.ml-2 { margin-left:0.75rem; }
.ml-3 { margin-left:1rem; }
.ml-4 { margin-left:1.25rem; }
.ml-5 { margin-left:1.5rem; }

.mr-0 { margin-right:0; }
.mr-1 { margin-right:0.5rem; }
.mr-2 { margin-right:0.75rem; }
.mr-3 { margin-right:1rem; }
.mr-4 { margin-right:1.25rem; }
.mr-5 { margin-right:1.5rem; }

.mt-0 { margin-top:0; }
.mt-1 { margin-top:0.5rem; }
.mt-2 { margin-top:0.75rem; }
.mt-3 { margin-top:1rem; }
.mt-4 { margin-top:1.25rem; }
.mt-5 { margin-top:1.5rem; }

.mb-0 { margin-bottom:0; }
.mb-1 { margin-bottom:0.5rem; }
.mb-2 { margin-bottom:0.75rem; }
.mb-3 { margin-bottom:1rem; }
.mb-4 { margin-bottom:1.25rem; }
.mb-5 { margin-bottom:1.5rem; }

.ms-x, .ml-x { margin-left:auto; }
.mx-0 { margin-left:0; margin-right:0; }
.mx-1 { margin-left:0.5rem; margin-right:0.5rem; }
.mx-2 { margin-left:0.75rem; margin-right:0.75rem; }
.mx-3 { margin-left:1rem; margin-right:1rem; }
.mx-4 { margin-left:1.25rem; margin-right:1.25rem; }
.mx-5 { margin-left:1.5rem; margin-right:1.5rem; }

.mx-auto { margin:0 auto; }

.w-100 { width:100%; }
.h-auto { height:auto; }
.border { border: 1px solid #dee2e6; }
.justify-content-center { justify-content: center; }
.border-top { border-top: 1px solid #dee2e6; }
.d-flex { display: flex; }
.col-12, .col-lg-12 { width:100%; flex-basis:100%; }
.col-6, .col-lg-6 { width:50%; flex-basis:50%; }
.col-3, .col-lg-3 { width:25%; flex-basis:25%; }
.col-lg-12 div.outer div, 
.col-lg-6 div.outer div { padding:0 12px; }
.flex-column { flex-direction: column; }

.tabbed_container .col-2 { background: #FFF; -webkit-box-shadow: 0px 2px 5px 0px rgb(209 211 212); -moz-box-shadow: 0px 2px 5px 0px rgba(209,211,212,1); box-shadow: 0px 2px 5px 0px rgb(209 211 212); }
.list-group { display: flex; flex-direction: column; padding-left: 0; margin-bottom: 0; border-radius: 0.25rem; }
.list-group-item { position:relative; display:block; padding:0; color:#212529; text-decoration:none; border:1px solid rgba(0,0,0,.125); overflow:hidden; }
.list-group-item + .list-group-item { border-top-width:0; }
.list-group-item:first-child { border-top-left-radius: inherit; border-top-right-radius: inherit; }
.list-group-item:last-child { border-bottom-right-radius:inherit; border-bottom-left-radius:inherit; }

.alert {
    position: relative;
    padding: 0.75rem 1.25rem;
    margin-bottom: 1rem;
    border: 1px solid transparent;
    border-radius: 0.25rem;
}
.alert-danger {
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
}

.d-block { display:block; }
.text-center { text-align:center; }
.btn {
    display: inline-block;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    background-color: transparent;
    border: 1px solid transparent;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    border-radius: 0.25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
.btn-sm {
    padding: 0.25rem 0.5rem;
    font-size: .875rem;
    border-radius: 0.2rem;
}
.btn-error,
.btn-primary, 
.btn-secondary { font-size:1em; text-decoration:none; }
.btn-error,
.btn-primary a { color:#FFF; }
.btn-primary, 
.btn-secondary, 
.btn-outline-primary, 
.btn-outline-secondary { color:#FFF; }
.btn-primary { background-color:#133C63; border-color:#133C63; }
.btn-secondary { background-color:#000; border-color:#000; }
.btn-error { background-color:#F00; border-color:#F00; }
.btn-outline-primary { background-color:transparent; border-width:4px; color:#133C63; }
.btn-outline-secondary { background-color:transparent; border-width:4px; color:#133C63; }
.btn-primary:hover, .btn-primary:active { background-color:#000; border-color:#000; color:#FFF; }
.btn-secondary:hover, .btn-secondary:active { background-color:#133C63; border-color:#133C63; color:#FFF; }
.btn-lg { font-size:1em; min-width:220px; }

.site-header { -webkit-box-shadow: 0px 2px 5px 0px rgb(209 211 212); -moz-box-shadow: 0px 2px 5px 0px rgba(209,211,212,1); box-shadow: 0px 2px 5px 0px rgb(209 211 212); max-width: 100%!important; background-color:#FFF; z-index:1000; }
.site-header { padding:10px 30px; margin:0; }
.site-header .contain { display:flex; flex-wrap:wrap; align-items:center; }
.site-header .logo img { max-width:140px; }
.site-header .wp-block-site-tagline { margin:0 0 0 20px; align-items:center; display:flex; font-size:0.85em; }
.site-header .wp-block-column + .wp-block-column { display: flex; justify-content:flex-end; }
.site-header .navbar { margin:0 0 0 auto; padding:0; }
.site-header .navbar ul { justify-content:end; }
.site-header .navbar ul ul { top:100%; margin:0; padding:0; }
.site-header .navbar .nav-item { display:flex; align-items:center; padding:0; margin-bottom:0; }
.site-header .navbar .nav-item svg { margin-left:6px; width:10px; height:auto; }
.site-header .navbar .nav-item svg polygon { fill:#133C63; }
.site-header .navbar a { display:inline-block; padding:16px; margin:0; color:#3C3C3C; font-weight:300; transition:color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out; }
.site-header .navbar .btn a.nav-link { color:#FFF; padding:18px 56px; }
.site-header .navbar a:hover { color:#000; }
.align-items-center { align-items:center; }
.navigation {
    padding-top: 10px;
    background: #FFF;
    -webkit-box-shadow: 0px 5px 5px 0px rgb(209 211 212);
    -moz-box-shadow: 0px 5px 5px 0px rgba(209,211,212,1);
    box-shadow: 0px 5px 5px 0px rgb(209 211 212);
}
.justify-content-between { justify-content: space-between; }
.formWrap { max-width:860px; }
.formSingleRow { display:flex; align-items:center; max-width:360px; }

.w-25, .w-33, .w-50, .w-75 { box-sizing: border-box; padding:10px; overflow:hidden; }
.w-25 div, .w-33 div, .w-50 div, .w-75 div { height:100%; box-sizing: border-box; display:flex; flex-direction:column; }
.w-25 p, .w-33 p, .w-50 p, .w-75 p { margin-top:0; }
.w-25 p:last-child, .w-33 p:last-child, .w-50 p:last-child, .w-75 p:last-child { margin-bottom:0; }
.w-25 { flex-basis:25%; width:25%; }
.w-33 { flex-basis:33.33334%; width:33.33334%; }
.w-50 { flex-basis:50%; width:50%; }
.title-link { display:block; color:#133C63; text-decoration:none; transition:color .15s ease-in-out,background-color.15s ease-in-out,border-color .15s ease-in-out; }
.checkList {
    border:solid 1px #CCC;
    border-radius:6px;
    padding:2px 12px;
    list-style-type:none;
}
.checkList li {
    border-bottom:solid 1px #CCC;
    display:flex;
}
.checkList li:last-child {
    border-bottom:0;
}
.checkList li input {
    margin-right:10px;
}
.checkList li label {
    display:block;
    width:100%;
    padding:10px 0;
}